body{
    font-family: 'Signika', sans-serif;
    padding:0;
    margin:0;
  }
  .navbar{
      height:auto;
      margin-top: 0;
      padding-top: 0%;
      width:100%;
      font-family: 'Signika', sans-serif;
  }
  .navbar-fixed-top.scrolled {
      background: #fff !important;
      transition: background-color 200ms linear;
  }
  
  .container-fluid{
      display: flex;
      width:40%;
      flex-direction: row;
      justify-content:space-between;
  }
  .logoimage{
    margin-left: 8px;
      height:60px;
      width:60px;
   }
   .navbar-collapse{
       display: flex;
       flex-direction: column;
       width: 10%;
       margin-left:45rem;
   }
   .nav-link{
       font-size: 1.2rem;
       margin-left:2rem;
   }

   .reviewnav{
     margin-right: 2rem;
     margin-left: 2rem;
   }

   @import url('https://fonts.googleapis.com/css?family=Catamaran');




   *, *:before, *:after {
    box-sizing: border-box;
  }
  
  
  a, a > span {
    position: relative;
    color: inherit;
    text-decoration: none;
    line-height: 24px;
  }
  a:before, a:after, a > span:before, a > span:after {
    content: '';
    position: absolute;
    transition: transform .5s ease;
  }
  .hover-1 {
    padding-top: 10px;
  }
  .hover-1:before {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background: #492059;
    transform: scaleX(0);
  }
  .hover-1:hover:before {
    transform: scaleX(1);
  }

.navbarcustom{
    background: white !important;
}
@media (max-width:980){
  .navbar-collapse{
    border: 2px solid black !important;
    background-color: chartreuse !important;
  }  
  .nav-link{
    
    border: 2px solid black !important;
  }
  .nav-items{
    border: 2px solid black !important;
  }
} 